  .chalkboard{
    font-size: 40px;
  color: #E8E8E8;
  font-family: "Walter Turncoat", cursive;
  display: block;

  height: 90%;
  min-height: auto;

  background-color: #497959;
  overflow-y: auto;
  box-shadow: -1px 2px 2px 0px #555, inset 0 0 10px 0 #555;
  border: #B78240 solid 10px;
  background-image: url('./assets/img/Chalkboard.txt');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.chakboard:after {
  content: "";
  display: block;
  position: fixed;
  border-radius: 0.1em 0.1em 0 0.1em;
  width: 50px;
  height: 8px;
  background: #f1f1f1;
  top: 80%;
  margin-top: 75px;
  margin-right: 60px;
  right: 5%;
  box-shadow: inset 0 -4px 1px rgba(0, 0, 0, 0.3), -1px -1px 1px rgba(0, 0, 0, 0.2), 0 2px 0 rgba(0, 0, 0, 0.3)
}


}
input::placeholder{
  transition: all 0.2s ease-in
}
